@import url(https://fonts.googleapis.com/css?family=Alegreya|Open+Sans+Condensed:300|Pacifico);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
ul.nav{
  display: flex;
  align-items: center;
  position: relative;
  /* top: 15px; */
}
ul.nav li{
  padding: 14px 26px;
  position: relative;
  /* top: 0; */
  /* height: 100%; */
}
ul.nav > li:hover{
    background-color: var(--primary-hover);
    /* color: var(--text-secondary) */

}
.subMenu{
    position: absolute;
    background-color: var(--primary-hover);
    right: 0px;
    top: calc(var(--header-height) - .95em);
    display: none;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}
.subMenu li:hover{
    background-color: var(--primary);
    border: 1px solid gray;
}
ul.nav > li:hover .subMenu{
    display: block;
}
 .HeaderContainer{
    box-sizing: border-box;
    position: fixed;
    top: 0;
    left: 0;
    background-color: var(--background);
    height: var(--header-height);
    width: 100%;
    box-shadow: 0 -15px 16px 10px black;
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
}
 .HeaderFrame{
    box-sizing: border-box;
    width:100%;
    height: var(--header-height);
    position: fixed;
    top: 0;
    left: 0;
 }
 .HeaderFrame .HeaderContainer{
     opacity: 0;
 }
 .HeaderFrame:hover .HeaderContainer{
     opacity: 1;
 }

img{
    height: 100%;
}
#CourseDetailContainer{
    box-sizing: border-box;
    position: absolute;
    overflow: hidden;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100%;
    background-color: grey;
}
#CoursesContainer{
    box-sizing: border-box;
    padding: 10px;
    width: 100vw;
    min-height: 100vh;
}

#CoursesContainer h1{
    text-align: center;
}
.courseContainer{
    padding: 5px 20px;
    background-color: red;
    margin: 10px 0;
    height: 200px;
    overflow: hidden;
    cursor: pointer;
    display: flex;
}
.descriptionContainer{
    padding: 20px;
}
.courseContainer:hover{
    background-color: grey;
}


#leftMenuContainer{
    padding: 20px 50px 20px 0;
    position: absolute;
    left: -250px;
    top: 5vh;
    width:90vw;
    max-width: 250px;
    background-color: gray;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    /* min-height: 80vh; */
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
}
#leftMenuContainer span{
    position: absolute;
    right: 0;
    top: 50%;
}
#leftMenuContainer:hover{
    left: 0;
}
#leftMenuContainer div{
    padding: 18px 50px;
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
}
#leftMenuContainer div:hover{
background-color: white;
}

 .FooterFrame{
    box-sizing: border-box;
    height: var(--footer-height);
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
 }
 .FooterFrame:hover .FooterContainer{
     opacity: 1;
 }

 .FooterContainer{
    box-sizing: border-box;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: var(--background);
    height: 100%;
    width: 100%;
    border-top: 3px solid black;
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
    opacity: 0;
}

img{
    height: 100%;
}
.HomeContainer{
    box-sizing: border-box;
    background-color: var(--background);
    min-height: 100vh;
    max-width: 100vw;
    /* overflow: hidden; */
}

iframe{
    position: absolute;
    top: 0;
    left: 0;
    margin: var(--header-height) 1%;
    overflow: hidden;
}
html, body, li, ul , div, span {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Josefin Sans', sans-serif;
  cursor: default;
}

/************          Variables         ***************/


:root {
  --c-green: #6F934C;
  --c-yellow: #E5B642;
  --background: var(--primary);
  --black: #020402;
  --red: #AF3236;
  --text-primary: #020402;
  --text-secondary: white;
  --text-primary-hover: rgb(54, 54, 54);
  --text-secondary-hover: #b9b9b9;
  --primary: whitesmoke;
  --primary-hover: rgba(192, 192, 192, 0.8);
  --secondary: rgb(109, 11, 11);
  --secondary-hover: rgb(100, 41, 41);
  --header-height: 60px;
  --footer-height: 40px;
}

/************          Global Styling         ***************/

html, body{
  color: #020402;
  color: var(--text-primary);
}
ul{
  display: inline;
}
input{
  font-size: 1em;
}
.hidden{
  display: none;
}
a, li{
  list-style-type: none;
  text-decoration: none;
}
:visited{
  color: #020402;
  color: var(--text-primary);
}
li:hover{
  /* color: var(--text-primary-hover) */
}

/************          Styling for custom components         ***************/




button1{
  color: var(--c-white);
  background-color: rgb(109, 11, 11);
  background-color: var(--secondary);
  border-radius: 25px;
  padding: .5em 2em;
  border: none;
}

button1:hover{
  background-color: rgb(100, 41, 41);
  background-color: var(--secondary-hover);
  box-shadow:0 3px 11px -2px black;
}
